import React from "react";
import s from './Nav.module.css'
import {Link} from "react-scroll";

type PropsType = {
    toggle: boolean
}
export const Nav = (props:PropsType) => {
    return(
        <nav className={props.toggle? `${s.navContainer} ${s.active}`:s.navContainer}>
                <ul className={s.list}>
                    <li>
                        <Link className={s.navLink} smooth={true}  duration={1000} to="home" >Home</Link>
                    </li>
                    <li>
                        <Link className={s.navLink} smooth={true} offset={-80} duration={1000} to="skill">Skills</Link>
                    </li>
                    <li><Link className={s.navLink} smooth={true} offset={-78} duration={1000} to="portfolio">Portfolio</Link></li>
                    <li><Link className={s.navLink} smooth={true} duration={1000} to="contacts">Contacts</Link></li>
                </ul>
        </nav>
    )
}